<template>
  <div class="manage_page fillcontain">

    <el-row style="height: 100%;">
      <el-col :span="4" style="min-height: 100%; background-color: #545c64;">
        <LeftView @msgFunc="lefeview_action"></LeftView>
      </el-col>
      <el-col :span="20" style="height: 100%;overflow: auto;">
        <keep-alive>
          <router-view></router-view>
        </keep-alive>

      </el-col>

    </el-row>

  </div>
</template>

<script>
  import LeftView from "./LeftView";

  export default {
    name: "Index",
    components: {LeftView},
    data() {
      return {
        show_index: "home"
      }
    },
    methods: {
      lefeview_action(msg) {
        console.log("child send " + msg)
        this.show_index = msg;
        this.$router.push(this.show_index)
      }
    },

    mounted() {
      this.$router.push(this.show_index)
    }

  }
</script>


<style lang="less" scoped>
  @import '../style/mixin';

  .manage_page {

  }

  .tab_content {
    display: flex;
    justify-content: center;
  }
</style>



