<template>
  <el-container>


      <el-header>
        <el-tabs v-model="activeName" @tab-click="handleClick" class="tabs">
          <el-tab-pane label="数据管理" name="first" class="tab"/>
          <el-tab-pane label="配置管理" name="second"></el-tab-pane>
          <el-tab-pane label="角色管理" name="third"></el-tab-pane>
          <el-tab-pane label="定时任务补偿" name="fourth"></el-tab-pane>
        </el-tabs>
      </el-header>
      <el-main>

        <div class="tab_content">

          <Tab1 v-if="showmsg==='first'"/>

          <Tab2 v-else-if="showmsg==='second'"/>
          <Tab3 v-else-if="showmsg==='third'"/>

        </div>

      </el-main>
    </el-container>

</template>


<script>

  import Tab1 from "./Tab1";
  import Tab2 from "./Tab2";
  import Tab3 from "./Tab3";


  export default {
    name: "Home",
    components: {Tab3, Tab2, Tab1},
    data() {
      return {
        activeName: 'first',
        showmsg: 'first'
      };
    },
    methods: {
      handleClick(tab, event) {
        console.log(tab.paneName);
        this.showmsg = tab.name
      }
    },
    comments: {
      Tab1
    }
  }

</script>

<style scoped>

  .tabs {
    background: aqua;
    display: flex;
    justify-content: center;
  }



  .el-header {
    line-height: 60px;
    padding: 0;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
  }


</style>



