import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)


import Home from "../components/home/Home";
import Index from "../components/Index";
import Action1 from "../components/action1/Action1";


const routes = [
  {
    path: '/',
    name: 'index',
    component: Index,
    children: [
      {
        path: '/home',
        name: 'home',
        component: Home
      },
      {
        path: '/action2',
        name: 'action2',
        component: Action1
      },
    ]
  },
]

export default new Router({
  routes,
})



